import { html, PolymerElement } from '@polymer/polymer/polymer-element.js';

/**
 * `editable-list`
 * `a listing of items that can be edited in place with operations`
 *
 * @microcopy - language worth noting:
 *  -
 *
 * @customElement
 * @polymer
 * @demo demo/index.html
 */
class EditableList extends PolymerElement {
  
  // render function
  static get template() {
    return html`
<style>:host {
  display: block;
}

:host([hidden]) {
  display: none;
}
</style>
<slot></slot>
<div>[[editMode]]</div>
<div>[[items]]</div>`;
  }

  // properties available to the custom element for data binding
  static get properties() {
    return {
  "editMode": {
    "name": "editMode",
    "type": "Boolean",
    "value": "false",
    "reflectToAttribute": true,
    "observer": "_editModeChanged"
  },
  "items": {
    "name": "items",
    "type": "Array",
    "value": "",
    "reflectToAttribute": false,
    "observer": "_itemsChanged"
  }
};
  }

  /**
   * Store the tag name to make it easier to obtain directly.
   * @notice function name must be here for tooling to operate correctly
   */
  static get tag() {
    return "editable-list";
  }
  /**
   * life cycle, element is afixed to the DOM
   */
  connectedCallback() {
    super.connectedCallback();
    
  }
  /**
   * life cycle, element is removed from the DOM
   */
  //disconnectedCallback() {}
    // Observer editMode for changes
              _editModeChanged (newValue, oldValue) {
                if (typeof newValue !== typeof undefined) {
                  console.log(newValue);
                }
              }

  // Observer items for changes
              _itemsChanged (newValue, oldValue) {
                if (typeof newValue !== typeof undefined) {
                  console.log(newValue);
                }
              }


}
window.customElements.define(EditableList.tag, EditableList);
export { EditableList };
